import { uniNoticeBar } from '../../components/uni-notice-bar/uni-notice-bar.vue';
export default {
	components: { uniNoticeBar },
	data() {
		return {
			user: {},
			token: ''
		};
	},
	onShow() {
		let _this = this;
		uni.getStorage({
			key: 'userInfo',
			success: function(res) {
				if (res.data) {
					_this.user = res.data;
					_this.user.img = '../../static/images/homeed.png';
				} else {
					uni.redirectTo({
						url: '../login/login'
					});
				}
			}
		});
		uni.getStorage({
			key: 'token',
			success: function(res) {
				_this.token = res.data;
			}
		});
	},
	methods: {
		goDetail(type) {
			let url = '';
			switch (type) {
				case 'peiyou':
					url = '../peiyou/peiyou';
					break;
				case 'ceping':
					url = '../ceping/ceping';
					break;
				case '书本':
					url = '../book/book';
					break;
				default:
					break;
			}
			url += '?list=' + JSON.stringify(this.user) + '&token=' + this.token;
			uni.navigateTo({
				url: url
			});
		}
	}
};
